$black:black;
$white:white;
*{
    margin: 0px;
    padding: 0px;
}
body{
    background-color: $black;
    font-family: Wicked Grit;}
header{
    display: flex;
    margin-left: 13vw;
    margin-top: 3vh;
}
.logo img{
    border-radius: 100%;
}
nav{
    margin-top: 3vh;
}
nav a{
    text-decoration: none;
    color: $white;
    
    margin: 8vw;
    
}
.user img{
    margin-left: 5vw;
    margin-top: 1vh;
}
.para p{
    color: $white;
    font-size: 64px;
    text-align: center;
    max-width: 76%;
    margin-top: 15%;
    margin-left: 13%;
}
.button{
    font-size: 48px;
    background-color: $white;
    max-width: 18%;
    border-radius: 30px;
    text-align: center;
    margin-top: 6%;
    margin-left: 42.5vw;
    cursor: pointer;
    transform: scale(1.2);
    transition: transform 0.6s,background-color 0.1s;
    &:hover {
        transform: scale(1.4);
    }
    &:active{
        background-color: $black;
        color: $white;
    }
}